import $ from "jquery";
import autosize from "autosize";
import datepicker from "js-datepicker";
import "jquery-mask-plugin";
import "selectric";
import "icheck";

const CustomForm = {
	initTextInputs() {
		$(document).on("focus", ".input-label-up__input", function () {
			$(this).parent().addClass("focus");
		});

		$(document).on("blur", ".input-label-up__input", function () {
			$(this).parent().removeClass("focus");
		});

		autosize($("textarea"));
	},

	initSelects() {
		$("select").selectric();
	},

	initDatepickers() {
		if ($(".input-datepicker input").length) {
			// throws error if element not found
			datepicker(".input-datepicker input", {
				customDays: ["", "", "", "", "", "", ""],
				customMonths: [
					"",
					"",
					"",
					"",
					"",
					"",
					"",
					"",
					"",
					"",
					"",
					"",
				],
				formatter: (input, date, instance) => {
					const value = new Intl.DateTimeFormat("ru-RU", {
						day: "2-digit",
						month: "2-digit",
						year: "2-digit",
					}).format(date);

					input.value = value;
				},
				overlayButton: "",
				overlayPlaceholder: " ",
				showAllDates: true,
			});
		}
	},

	initCheckboxes() {
		$(document).on("ifCreated ifToggled", "input", function () {
			const isChecked = $(this).prop("checked");
			$(this).closest("label").toggleClass("active", isChecked);
		});

		$("input").iCheck();
	},

	init() {
		this.initCheckboxes();
		this.initDatepickers();
		this.initTextInputs();
		this.initSelects();
	},
};

$(function () {
	CustomForm.init();
});

export default CustomForm;
